import React, { useState, useEffect } from "react";
import { Heart, ShieldCheck, CheckCircle, Share2, Lock, AlertTriangle, Loader2 } from "lucide-react";

// API Configuration - Vercel will auto-detect this
const API_URL = typeof window !== 'undefined' 
  ? (window.location.hostname === 'localhost' 
      ? 'http://localhost:3001/api' 
      : '/api')
  : '/api';

export default function HeartBuddyLaunch() {
  const [phone, setPhone] = useState("");
  const [signedIn, setSignedIn] = useState(false);
  const [currentUser, setCurrentUser] = useState(null);
  const [checkPhone, setCheckPhone] = useState("");
  const [checkResult, setCheckResult] = useState(null);
  const [partnerPhone, setPartnerPhone] = useState("");
  const [relationships, setRelationships] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);

  // Load user from localStorage on mount
  useEffect(() => {
    const savedUser = localStorage.getItem("heartBuddyUser");
    if (savedUser) {
      const user = JSON.parse(savedUser);
      setCurrentUser(user);
      setSignedIn(true);
      setPhone(user.phone);
      loadUserRelationships(user.phone);
    }
  }, []);

  // Auto-clear messages after 5 seconds
  useEffect(() => {
    if (error || success) {
      const timer = setTimeout(() => {
        setError(null);
        setSuccess(null);
      }, 5000);
      return () => clearTimeout(timer);
    }
  }, [error, success]);

  // Load user relationships
  const loadUserRelationships = async (userPhone) => {
    try {
      const response = await fetch(`${API_URL}/relationships/${userPhone}`);
      const data = await response.json();
      if (response.ok) {
        setRelationships(data.relationships);
      }
    } catch (err) {
      console.error("Failed to load relationships:", err);
    }
  };

  // Handle sign up/login
  const handleSignUp = async () => {
    setError(null);
    setSuccess(null);
    setLoading(true);

    try {
      const response = await fetch(`${API_URL}/auth/signup`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ phone }),
      });

      const data = await response.json();

      if (!response.ok) {
        setError(data.error);
        setLoading(false);
        return;
      }

      setCurrentUser(data.user);
      setSignedIn(true);
      localStorage.setItem("heartBuddyUser", JSON.stringify(data.user));
      setSuccess(data.message);
      loadUserRelationships(data.user.phone);
    } catch (err) {
      setError("Failed to connect to server. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  // Handle checking someone
  const handleCheck = async () => {
    setError(null);
    setSuccess(null);
    setCheckResult(null);
    setLoading(true);

    try {
      const response = await fetch(`${API_URL}/check`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          checkerPhone: currentUser.phone,
          checkPhone: checkPhone,
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        setError(data.error || data.message);
        setLoading(false);
        return;
      }

      setCheckResult(data.result);
      setCurrentUser({ ...currentUser, freeChecksRemaining: data.freeChecksRemaining });
      localStorage.setItem("heartBuddyUser", JSON.stringify({
        ...currentUser,
        freeChecksRemaining: data.freeChecksRemaining
      }));
    } catch (err) {
      setError("Failed to perform check. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  // Handle declaring relationship
  const handleDeclareRelationship = async () => {
    setError(null);
    setSuccess(null);
    setLoading(true);

    try {
      const response = await fetch(`${API_URL}/relationships/declare`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          initiatorPhone: currentUser.phone,
          partnerPhone: partnerPhone,
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        setError(data.error);
        setLoading(false);
        return;
      }

      setSuccess("Relationship declaration sent! Waiting for partner approval.");
      setPartnerPhone("");
      loadUserRelationships(currentUser.phone);
    } catch (err) {
      setError("Failed to declare relationship. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  // Handle approving relationship
  const handleApproveRelationship = async (relationshipId) => {
    setError(null);
    setSuccess(null);
    setLoading(true);

    try {
      const response = await fetch(`${API_URL}/relationships/approve`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          partnerPhone: currentUser.phone,
          relationshipId: relationshipId,
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        setError(data.error);
        setLoading(false);
        return;
      }

      setSuccess("Relationship verified successfully!");
      loadUserRelationships(currentUser.phone);
    } catch (err) {
      setError("Failed to approve relationship. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  // Handle privacy toggle
  const handleTogglePrivacy = async (relationshipId, currentIsPublic) => {
    try {
      const response = await fetch(`${API_URL}/relationships/${relationshipId}/privacy`, {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          isPublic: !currentIsPublic,
          userPhone: currentUser.phone,
        }),
      });

      const data = await response.json();

      if (response.ok) {
        setSuccess(data.message);
        loadUserRelationships(currentUser.phone);
      } else {
        setError(data.error);
      }
    } catch (err) {
      setError("Failed to update privacy settings.");
    }
  };

  // Handle share certificate
  const handleShareCertificate = (certificateId) => {
    const url = `${window.location.origin}/certificate/${certificateId}`;
    navigator.clipboard.writeText(url);
    setSuccess("Certificate link copied to clipboard!");
  };

  // Handle sign out
  const handleSignOut = () => {
    localStorage.removeItem("heartBuddyUser");
    setSignedIn(false);
    setCurrentUser(null);
    setPhone("");
    setRelationships([]);
    setCheckResult(null);
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-rose-50 to-white text-gray-900">
      {/* HEADER */}
      <header className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-6 py-4 flex justify-between items-center">
          <div className="flex items-center gap-2 text-pink-600 font-bold text-xl">
            <Heart className="w-6 h-6" /> Heart Buddy
          </div>
          {!signedIn ? (
            <button className="bg-pink-600 text-white px-4 py-2 rounded-lg">
              Sign up
            </button>
          ) : (
            <div className="flex items-center gap-4">
              <span className="text-sm text-gray-600">
                {currentUser?.phone} • {currentUser?.freeChecksRemaining} free checks left
              </span>
              <button
                onClick={handleSignOut}
                className="text-sm text-gray-600 hover:text-pink-600"
              >
                Sign out
              </button>
            </div>
          )}
        </div>
      </header>

      {/* ALERTS */}
      {error && (
        <div className="max-w-7xl mx-auto px-6 py-4">
          <div className="bg-red-50 border border-red-200 text-red-800 px-4 py-3 rounded-lg flex items-center gap-2">
            <AlertTriangle className="w-5 h-5" />
            {error}
          </div>
        </div>
      )}
      {success && (
        <div className="max-w-7xl mx-auto px-6 py-4">
          <div className="bg-green-50 border border-green-200 text-green-800 px-4 py-3 rounded-lg flex items-center gap-2">
            <CheckCircle className="w-5 h-5" />
            {success}
          </div>
        </div>
      )}

      {/* HERO */}
      <section className="max-w-7xl mx-auto px-6 py-20 grid md:grid-cols-2 gap-12 items-center">
        <div>
          <h1 className="text-5xl font-extrabold leading-tight">
            Protect Your Heart. Avoid Heartbreaks.
            <br />
            Prevent Cheating.
          </h1>
          <p className="mt-6 text-gray-600 text-lg">
            Heart Buddy helps singles and couples date with clarity. Check if
            someone is already taken, declare relationships safely, detect trust
            risks, and protect your emotional peace before getting emotionally
            invested.
          </p>
          <div className="mt-8 flex gap-4">
            <a href="#check" className="bg-pink-600 text-white px-6 py-3 rounded-xl">
              Check Someone
            </a>
            <a href="#declare" className="border px-6 py-3 rounded-xl">
              Declare Relationship
            </a>
          </div>
        </div>

        {!signedIn && (
          <div className="bg-white rounded-2xl shadow-xl p-6">
            <h3 className="font-semibold mb-3">Sign up with your phone number</h3>
            <input
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
              placeholder="e.g., +254712345678"
              className="w-full border px-4 py-3 rounded-xl mb-3"
              disabled={loading}
            />
            <button
              onClick={handleSignUp}
              disabled={loading || !phone}
              className="w-full bg-pink-600 text-white py-3 rounded-xl disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2"
            >
              {loading ? (
                <>
                  <Loader2 className="w-5 h-5 animate-spin" />
                  Processing...
                </>
              ) : (
                "Continue"
              )}
            </button>
            <p className="text-xs text-gray-500 mt-3">
              No profiles. No oversharing. Just trust.
            </p>
          </div>
        )}
      </section>

      {/* CHECK SOMEONE */}
      {signedIn && (
        <section id="check" className="max-w-7xl mx-auto px-6 py-16">
          <div className="bg-white rounded-2xl shadow p-6 max-w-xl">
            <h3 className="text-xl font-bold flex items-center gap-2 mb-3">
              <ShieldCheck className="w-5 h-5 text-pink-600" /> Check If Someone
              Is Taken
            </h3>
            <p className="text-gray-600 mb-4">
              Enter a phone number to see whether a relationship has already been
              declared. We never show private details.
            </p>
            <input
              value={checkPhone}
              onChange={(e) => setCheckPhone(e.target.value)}
              placeholder="Enter phone number (e.g., +254712345678)"
              className="w-full border px-4 py-3 rounded-xl mb-3"
              disabled={loading}
            />
            <button
              onClick={handleCheck}
              disabled={loading || !checkPhone}
              className="bg-pink-600 text-white w-full py-3 rounded-xl disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2"
            >
              {loading ? (
                <>
                  <Loader2 className="w-5 h-5 animate-spin" />
                  Checking...
                </>
              ) : (
                "Run Check"
              )}
            </button>
            {checkResult && (
              <div className="mt-4 p-4 rounded-xl border bg-gray-50">
                <strong className="block mb-2">Status: {checkResult.status}</strong>
                {checkResult.trustSignals && (
                  <div className="mt-2 space-y-2">
                    <p className="text-sm font-semibold text-orange-600">
                      Trust Signals:
                    </p>
                    {checkResult.trustSignals.map((signal, idx) => (
                      <div
                        key={idx}
                        className="text-sm bg-orange-50 p-2 rounded border border-orange-200"
                      >
                        <strong className="capitalize">{signal.type.replace(/_/g, " ")}:</strong>{" "}
                        {signal.description}
                      </div>
                    ))}
                  </div>
                )}
                {checkResult.details && checkResult.details.verifiedAt && (
                  <p className="text-sm text-gray-600 mt-2">
                    Verified on{" "}
                    {new Date(checkResult.details.verifiedAt).toLocaleDateString()}
                  </p>
                )}
              </div>
            )}
            <p className="text-xs text-gray-500 mt-2">
              {currentUser?.freeChecksRemaining} free checks remaining. Then KES 100
              per search.
            </p>
          </div>
        </section>
      )}

      {/* DECLARE RELATIONSHIP */}
      {signedIn && (
        <section id="declare" className="max-w-7xl mx-auto px-6 py-16">
          <div className="bg-white rounded-2xl shadow p-6 max-w-xl">
            <h3 className="text-xl font-bold mb-3">Declare a Relationship</h3>
            <p className="text-gray-600 mb-4">
              Declaring a relationship is simple. Enter your partner's phone number
              and they'll receive a request to approve. Once approved, you both
              receive a verified digital relationship certificate.
            </p>
            <input
              value={partnerPhone}
              onChange={(e) => setPartnerPhone(e.target.value)}
              placeholder="Partner phone number (e.g., +254712345678)"
              className="w-full border px-4 py-3 rounded-xl mb-3"
              disabled={loading}
            />
            <button
              onClick={handleDeclareRelationship}
              disabled={loading || !partnerPhone}
              className="bg-pink-600 text-white px-6 py-3 rounded-xl disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2"
            >
              {loading ? (
                <>
                  <Loader2 className="w-5 h-5 animate-spin" />
                  Processing...
                </>
              ) : (
                "Declare Relationship"
              )}
            </button>
          </div>
        </section>
      )}

      {/* MY RELATIONSHIPS */}
      {signedIn && relationships.length > 0 && (
        <section className="max-w-7xl mx-auto px-6 py-16">
          <div className="bg-white rounded-2xl shadow p-6">
            <h3 className="text-xl font-bold mb-4">My Relationships</h3>
            <div className="space-y-4">
              {relationships.map((rel) => {
                const isInitiator = rel.initiator_phone === currentUser.phone;
                const partnerPhone = isInitiator
                  ? rel.partner_phone
                  : rel.initiator_phone;

                return (
                  <div
                    key={rel.id}
                    className="border rounded-xl p-4 bg-gray-50"
                  >
                    <div className="flex justify-between items-start mb-2">
                      <div>
                        <p className="font-semibold">
                          {isInitiator ? "You declared" : "Declared by"}: {partnerPhone}
                        </p>
                        <p className="text-sm text-gray-600">
                          Status:{" "}
                          <span
                            className={
                              rel.status === "verified"
                                ? "text-green-600 font-semibold"
                                : "text-yellow-600 font-semibold"
                            }
                          >
                            {rel.status === "verified" ? "✓ Verified" : "Pending Approval"}
                          </span>
                        </p>
                      </div>
                      {rel.status === "verified" && (
                        <span
                          className={`text-xs px-2 py-1 rounded ${
                            rel.is_public
                              ? "bg-blue-100 text-blue-700"
                              : "bg-gray-200 text-gray-700"
                          }`}
                        >
                          {rel.is_public ? "Public" : "Private"}
                        </span>
                      )}
                    </div>

                    {rel.status === "pending" && !isInitiator && (
                      <button
                        onClick={() => handleApproveRelationship(rel.id)}
                        className="mt-3 bg-green-600 text-white px-4 py-2 rounded-lg text-sm"
                      >
                        Approve Relationship
                      </button>
                    )}

                    {rel.status === "verified" && (
                      <div className="mt-3 flex gap-3 flex-wrap">
                        <button
                          onClick={() => handleShareCertificate(rel.certificate_id)}
                          className="border px-4 py-2 rounded-lg flex items-center gap-2 text-sm"
                        >
                          <Share2 className="w-4 h-4" /> Share Certificate
                        </button>
                        <button
                          onClick={() => handleTogglePrivacy(rel.id, rel.is_public)}
                          className="border px-4 py-2 rounded-lg flex items-center gap-2 text-sm"
                        >
                          <Lock className="w-4 h-4" />
                          {rel.is_public ? "Make Private" : "Make Public"}
                        </button>
                      </div>
                    )}
                  </div>
                );
              })}
            </div>
          </div>
        </section>
      )}

      {/* FOOTER */}
      <footer className="text-center text-sm text-gray-500 py-10 border-t bg-white">
        © 2026 Heart Buddy – We Prevent Cheating, Not Peace
      </footer>
    </div>
  );
}
